*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --accent-clr: #f8d24e;
  --bg-main-clr: #84a178;
  --bg-secondary-clr: #fcfcfc;
  --bg-product-one-clr: #aec6a5;
  --bg-product-one-clr-alpha: rgba(156, 190, 143, .7);
  --bg-product-two-clr: #efd478;
  --bg-product-two-clr-alpha: rgba(248, 210, 78, .7);
  --bg-product-three-clr: #d6936d;
  --bg-product-three-clr-alpha: rgba(213, 117, 63, .7);
  --text-main-clr: #131313;
  --text-secondary-clr: #fcfcfc;
  --text-contacts-clr: #d6936d;
  --text-contacts-secondary-clr: #ababab;
  --text-footer-clr: #7a7a7a;
  --text-white-color: #fff;
  --font-main: "Montserrat", Arial, sans-serif;
  --font-secondary: "Seymour One", "Arial Black", sans-serif;
  --input-border-clr: #84a178;
  --input-bg-clr: rgba(248, 210, 78, .1);
  --mobile-menu-bg-clr: #85a279;
  --tablet-menu-bg-clr: #97af8d;
  --btn-text-clr: #000;
  --btn-hover-focus-bg-clr: #0a8817;
  --btn-text-focus-clr: #fcfcfc;
  --btn-disabled-clr: #ababab;
  --icon-black-clr: #000;
  --icon-socials-clr: #84a178;
  --icon-hover-focus-clr: #fcfcfc;
  --transition-time: 250ms;
  --transition-cubic: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

address {
  font-style: normal;
}

body {
