.btn {
  color: var(--btn-text-clr);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
  display: inline-flex;
}

.btn--bordered {
  border: 2px solid var(--accent-clr);
  transition-property: border-color, color;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--transition-cubic);
}

.btn--bordered:hover, .btn--bordered:focus {
  border-color: var(--btn-hover-focus-bg-clr);
}

.btn--bordered:focus {
  color: var(--btn-text-focus-clr);
}

.btn--bordered:disabled {
  border-color: var(--btn-disabled-clr);
}

.btn--bordered-sm {
  min-width: 108px;
  min-height: 38px;
  font-size: 12px;
  line-height: 1.16667;
}

.btn--bordered-md {
  min-width: 134px;
  min-height: 42px;
}

.btn--bordered-lg {
  min-width: 196px;
  min-height: 42px;
}

.btn--with-icon .btn__text {
  margin-right: 4px;
}

.btn--with-icon .btn__icon {
  fill: currentColor;
}

.btn--with-bg {
  min-height: 42px;
  background-color: var(--accent-clr);
  transition-property: background-color, color;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--transition-cubic);
}

.btn--with-bg:hover, .btn--with-bg:focus {
  background-color: var(--btn-hover-focus-bg-clr);
}

.btn--with-bg:focus {
  color: var(--btn-text-focus-clr);
}

.btn--with-bg:disabled {
  background-color: var(--btn-disabled-clr);
}

.btn--with-bg-sm {
  min-width: 152px;
}

.btn--with-bg-md {
  min-width: 164px;
}

.btn--with-bg-lg {
  min-width: 196px;
}

.btn--text-decorated {
  color: var(--accent-clr);
  transition-property: color;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--transition-cubic);
  text-decoration: underline;
}

.btn--text-decorated:hover, .btn--text-decorated:focus {
  color: var(--btn-hover-focus-bg-clr);
}

.btn--text-decorated:focus {
  color: var(--btn-text-focus-clr);
}

.btn--text-decorated:disabled {
  color: var(--btn-disabled-clr);
}

.btn--text-decorated-sm {
  min-width: 106px;
  min-height: 24px;
  padding: 5px;
  font-size: 12px;
  line-height: 1.25;
}

.btn--text-decorated-md {
  min-width: 154px;
  min-height: 40px;
}

